
.search-box{
  width: 100%;
  background: #2C3E50;
  padding: 20px;
  border-radius: 4px;
}
.btn-primary{
  background: #19B5FE;
  border: #19B5FE 0 solid;
}
.btn-primary:hover{
  background: #19B5FE;
}
.form-control{
  border-radius:3px;
}
.tai-tit{
  color: #fff;
  font-size:10em;
  padding-top: 300px;
  padding-left:35%;
}
.margin-sikit{
  margin-top: 20px;
}
.list-complete-item {
  transition: all 1s;
  display: inline-block;
  margin-right: 10px;
}
.list-complete-enter, .list-complete-leave-to
/* .list-complete-leave-active for <2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
.list-complete-leave-active {
  position: absolute;
}
.mrt{
  margin-top:10px;
}



